@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --color-1: #FFB0B0;
    --color-2: #0C2D57;
    --color-3: #EFECEC;
    --color-4: #FC6736;
    --box-shadow: 0 2rem 3rem var(--color-light);
}

body {
    width: 100%;
    height: 100vh;
    font-family: 'Roboto', sans-serif;
}

.container {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--color-1);
}

.wapper {
    background-color: white;
    padding: 1rem 1.2rem;
    box-shadow: var(--box-shadow);
    border-radius: 1rem;
    width: 70rem;
    height: 40rem;
    margin: 1rem;
    position: relative;
}

header {
    padding: 0.5rem 0.5rem;
    color: white;
    background-color: var(--color-2);
    margin-bottom: 1rem;
}

.chat-conversition {
    width: 100%;
    /* background-color: var(--color-3); */
    overflow-y: auto;
    overflow-x: hidden;
    height: 32rem;
    padding: 1rem;
}

.chat {
    /* background-color: red; */
    margin: 1rem 0;
    width: 100%;
    /* margin-right: 1.2rem; */
}

.bot-chat {
    display: flex;
    width: 100%;
    justify-content: flex-start;
}

.user-chat {
    display: flex;
    width: 100%;
    justify-content: flex-end;
}

.chat-container {
    max-width: 40rem;
    padding: 0.5rem;
    background-color: black;
    color: white;
}

.input-box {
    position: absolute;
    bottom: 0;
    width: 100%;
    background-color: red;
    left: 0;
    padding: 0.5rem 1.2rem;
}

.input-box input {
    width: calc(100% - 5rem);
    height: 3rem;
    background-color: var(--color-1);
    color: black;
    border: none;
    outline: none;
    padding: 0 0.5rem;
}

.input-box input::placeholder {
    color: black;
}

.input-box button {
    background-color: transparent;
    border: none;
    outline: none;
    width: 5rem;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.input-box button i {
    color: black;
}